<template>
    <div class="module_head__title">
        <h2>{{tr.header}}</h2>
        <p>{{tr.advice}}</p>
        <div class="connection">
            <div class="connection__info">
                <div class="connection__icon">
                    <img src="../../assets/info_icon.svg" alt="">
                </div>
                <div class="connetion__texts">
                    <p>{{tr.connection}}</p>
                    <p>{{tr.todo}}</p>
                    <div class="connection__info__support">
                        <ul>
                            <li ><a  href="#" @click.prevent="downloadZip"><img src="../../assets/download.svg"
                                                                              alt=""><span>{{tr.download}}</span></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="connection__source">

                <div class="form-group connection__source_form">
                    <!--<label>Source store URL:</label>-->
                    <div role="group">
                        <label for="inputLive">{{tr.url}}</label>
                        <b-form-input id="inputConnection" v-if="tr.demo_mode == null || !tr.demo_mode"
                                      class="connection__input"
                                      v-model="sourceUrl"
                                      :value="sourceShopUrl"
                                      type="text"
                                      :state="false"
                                      aria-describedby="inputLiveHelp inputLiveFeedback"
                                      v-bind:placeholder="tr.source_url_placeholder"></b-form-input>
                        <b-form-input id="inputConnection" v-if="tr.demo_mode"
                                      class="connection__input"
                                      v-model="tr.demo_url"
                                      type="text"
                                      :state="false"
                                      :disabled="tr.demo_mode"
                                      aria-describedby="inputLiveHelp inputLiveFeedback"
                                      v-bind:placeholder="tr.source_url_placeholder"></b-form-input>
                        <div class="connection_input__holder">
                            <img v-if="regCheck" src="../../assets/check.svg" alt="">
                            <img v-if="regCheck==null" src="../../assets/link.svg" alt="">
                            <img v-if="regCheck==false" src="../../assets/close_red.svg" alt="">
                        </div>
                    </div>
                    <span class="connection_status" v-if="regCheck==null">{{tr.hint}}</span>
                    <div class="connection_status_error" v-for="error in connectionErrorMessage" v-else  v-html='error'>
                    </div>
                    <div class="connection_status_error">{{connectionFatalErrorMessage ? tr.fatal_error : ""}}</div>

                </div>
                <div class="connect__next_step">
                    <button @click="connectionStatusClick" class="next_step__button">{{tr.next}}</button>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapMutations} from 'vuex';
    import {mapState} from 'vuex';
    import {store} from '../../store/store'


    import * as types from '../../store/type'


    export default {
        name: 'Connection',

        destroyed() {
//           Remove initial error state when component will destroy
            this.$store.commit(types.MUTATE_REG_STATUS, null)
            this.$store.commit(types.MUTATE_CONNECTION_FATAL_ERROR_MESSAGE, false)
        },
        computed: {
            ...mapGetters({
                regCheck: types.REG_STATUS, // get error and input state
//                connectionStatus: types.CONNECTION_STATUS,
                sourceShopUrl: types.SOURCE_SHOP_URL, // get source shop url
                connectionErrorMessage: types.CONNECTION_ERROR_MESSAGE, //get error message first step
                connectionFatalErrorMessage: types.CONNECTION_FATAL_ERROR_MESSAGE, //get error message first step
                tr: 'step_first_trans', // get frist step  tranlations
            }),
//            this method use for input url (v-model)
            sourceUrl: {
                get() {
                    return this.$store.getters.sourceShopUrl;
                },
                set(value) {
                    this.$store.commit(types.MUTATE_SOURCE_URL, value);
                }
            },

        },
        methods: {
            ...mapActions({
                connectionStatusClick: types.CLICK_CONNECTION_STATUS, // click connection button and move step 2
//                changeComponent: 'changeComponent',
                downloadZip: types.CLICK_GENERATE_ZIP, //download zip
            }),
        },
    }
</script>
<style lang="scss" scoped>
    @import "../../scss/main";

        .connection__input {
            font-size: 14px !important;
        }

        .connection_status {
            font-style: normal !important;
            font-weight: 400 !important;
            font-size: 12px !important;
        }

        .connection {
            box-shadow:  0 3px 6px 0 rgba(0, 0, 0, 0.1);
            margin-top: 30px;
            font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
            .connection__icon {
                margin-right: 15px;
            }
            .connection__info {
                background: white;
                padding: 25px 48px 0;
                display: flex;
            }
            .connection__source {
                background-color: #f5f6f7;
            }
            .connection_status_error {
                color: red;
                font-style: normal;
                font-size: 12px;
                font-weight: 400;
                line-height: 1.73;
                margin-top: 4px;
                &:first-child {
                    margin-top: 5px;
                }
            }
            .connetion__texts {
                width: 100%;
                p {
                    margin: 0 0 12px 0 !important;
                    &:first-child {
                        font-size: 18px;
                        line-height: 1;
                    }
                    &:nth-child(2) {
                        font-size: 14px;
                        color: #59626a;
                    }
                }
            }
            .connection__info__support {
                ul {
                    padding: 0;
                    margin: 0;
                    display: flex;
                    list-style: none;
                    li {
                        img {
                            margin-bottom: 3px;
                            margin-right: 5px;
                        }
                        span {
                            font-size: 14px;
                            letter-spacing: 0.5px;
                            color: #627381;
                        }
                        a {
                            border-bottom: 2px solid white;
                            &:hover {
                                border-color: $hover-border-color;
                                transition: .1s ease-in-out;
                                box-sizing: border-box;
                            }
                            display: block;
                            padding-bottom: 15px;
                        }
                        margin-right: 20px;

                    }
                }
            }
            .connection__source {
                .connection__source_form {
                    padding: 25px 45px 25px 45px;

                    margin-bottom: 0;
                    & > div {
                        position: relative;
                    }
                    .connection_input__holder {
                        position: absolute;
                        top: 27px;
                        right: 15px;

                        img {
                            margin-top: 5px;
                        }
                    }
                }
                label {
                    margin: 0;
                    font-size: 14px;
                    color: #212b36;
                }
                span {
                    font-style: italic;
                    font-size: 11px;
                    font-weight: 300;
                    color: #212b36;
                    line-height: 1.73;
                    padding-left: 10px;
                }
                input {
                    /*background-image: url(../../assets/link.svg);*/
                    background-position: right;
                    background-position: bottom 10px right 17px;
                    background-repeat: no-repeat;
                }
            }
            .connect__next_step {
                padding: 50px;
                border-top: solid 1px #eaeef3;
                display: flex;
                justify-content: flex-end;
            }
        }

    .connect__next_step {
        padding: 20px 45px !important;
    }
</style>
