import * as types from '../type'

const state = {
    migrationEntities: {
        migratedData: false,
        toggleWarning: true,
        migration: {},
        migrationPause: false,
        migrationPauseClicked: false,
        attentionModal: false
    },
    time_remaining: '00:00:00',
    migrationFatalError:false,
    migrationWarningMessage : '',
    has_error: false,
    migration_paused: false
    
}
const getters = {
    [types.MIGRATED_DATA]: (state, getters, rootGetters) => {
        return state.migrationEntities.migration;
    },
    [types.TOGGLE_WARNING]: (state, getters, rootGetters) => {
        return state.migrationEntities.toggleWarning;
    },
    [types.MIGRATION_PAUSE]: (state, getters, rootGetters) => {
        return state.migrationEntities.migrationPause;
    },
    [types.ATTENTION_MODAL_VISIBILITY]: (state, getters, rootGetters) => {
        return state.migrationEntities.attentionModal;
    },
    [types.TIME_REMAINING]: (state, getters, rootGetters) => {
        return state.time_remaining;
    },
    [types.MIGRATION_FATAL_ERROR]: (state, getters, rootGetters) => {
        return state.migrationFatalError; 
    },
    [types.TOGLEE_WARNING_MESSAGE]: (state, getters, rootGetters) => {
        return state.migrationWarningMessage;
    },

}

const mutations = {
    [types.MUTATE_TOGGLE_WARNING]: (state, payload) => {
        return state.migrationEntities.toggleWarning = payload;
    },
    [types.MUTATE_MIGRATED_DATA]: (state, migrate_data) => {
        return state.migrationEntities.migration = migrate_data;
    },
    [types.MUTATE_MIGRATION_PAUSE]: (state, payload) => {
        return state.migrationEntities.migrationPause = payload;
    },
    [types.MUTATE_MIGRATION_PAUSE_CLICKED]: (state, payload) => {
        return state.migrationEntities.migrationPauseClicked = payload;
    },
    [types.MUTATE_OPEN_ATTETION_MODAL]: (state, payload) => {
        return state.migrationEntities.attentionModal = payload;
    },
    [types.MUTATE_CLOSE_ATTETION_MODAL]: (state, payload) => {
        return state.migrationEntities.attentionModal = payload;
    },
    [types.MUTATE_TIME_REMAINING]: (state, payload) => {
        return state.time_remaining = payload;
    },
    [types.MUTATE_MIGRATION_FATAL_ERROR]: (state, payload) => {
        return state.migrationFatalError = payload;
    },
    [types.MUTATE_TOGLEE_WARNING_MESSAGE]: (state, payload) => {
        return state.migrationWarningMessage = payload;
    },
}
watch: {
    
    deep: true
}
const actions = {
    [types.CLICK_TOGGLE_WARNING]: ({commit}, payload) => {
        commit(types.MUTATE_TOGGLE_WARNING, payload)
    },
    
    [types.UPDATE_MIGRATION_STATES]: ({commit, state, dispatch}, stepData) => {
        let data_migrate = {}
        let set_first = false;
        let pause = stepData.pause;
        stepData = stepData.step_form
        for (let i in stepData) {
            


            if(stepData[i]['finish'] == 1)
            stepData[i]['status'] = 'finished';
            else
            {
                if(!set_first)
                    stepData[i]['status'] = 'paused';
                else
                stepData[i]['status'] = '';
            
                set_first = true;
            }


            Object.assign(data_migrate, {
                [stepData[i].type]: {
                    ...stepData[i]
                }
            })
        }
        commit('mutateSelectedComponent', 'app-migration')        
        commit(types.MUTATE_MIGRATED_DATA, data_migrate)
        if(!pause)
        { 
            commit(types.MUTATE_MIGRATION_PAUSE, false)

            dispatch(types.CLICK_START_MIGRATION_PROCESS,'contiune')            
        }
        else
        {
            commit(types.MUTATE_MIGRATION_PAUSE, true)
        }
    },
    [types.CLICK_START_MIGRATION_PROCESS]: ({commit, state, dispatch}, payload) => {
        let mg_data = {};
        let set_first = false;        
        let play_migration = document.querySelector('.m_play_migration')
        let pause_migration = document.querySelector('.m_pause_migration')
        let pause_animation = document.querySelector('.migrating_load')
        commit(types.MUTATE_MIGRATION_PAUSE, false)

        if (pause_migration != null)
            pause_migration.classList.remove("disable_button")
        if (play_migration != null)
            play_migration.classList.add("disable_button")
        let last_migration_data = {};

        for(let m in state.migrationEntities.migration) {
            let data = state.migrationEntities.migration[m];
            
            if(data['status'] == 'error' && state.has_error) {
                pause_migration.classList.add("disable_button")
                play_migration.classList.remove("disable_button")
                if(state.has_error)
                {
                    state.has_error = false;
                    return;
                }
                else
                    state.migrationEntities.migration[m]['status'] = '';

            }
            if(data['finish'] == 1)
                data['status'] = 'finished';
            else
            {
                if(!set_first)
                    data['status'] = 'continue';
                else
                    data['status'] = '';
            
                set_first = true;
            }
            Object.assign(last_migration_data, {
                [m]: {
                    ...data
                }
            })
        }
        
        commit(types.MUTATE_MIGRATED_DATA, last_migration_data)

        if (payload == 'contiune') {
            if (pause_migration != null)
                pause_migration.classList.remove("disable_button")
            if (play_migration != null)
                play_migration.classList.add("disable_button")
            // pause_animation.classList.remove("stop-animation")
        }
        if (pause_animation != null) {
            pause_animation.classList.remove("stop-animation")
        }

        commit(types.MUTATE_MIGRATION_FATAL_ERROR, false)

        let mg_all_done = false
        let formData = new FormData()
        formData.append('action', 'import_process');
        formData.append('ajax', 'true');
        formData.append('status', payload);
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    commit(types.MUTATE_MIGRATION_FATAL_ERROR, true)
                    // return;
                    // commit(types.MUTATE_MIGRATION_FATAL_ERROR, 'Fatal error')
                    // commit(types.MUTATE_MIGRATED_DATA, state.migrationEntities.migration)
                    // throw new Error('Something went wrong');
                }
            })
            .then((data) => {
                // commit('mutateSelectedComponent', 'app-migration')
                set_first = false;
                for (let i in data.percent) {
                   
                    

                    if(data.percent[i]['finish'] == 1){
                        data.percent[i]['status'] = 'finished';
                    }
                    else
                    {
                        if(!set_first){
                            if(!data.pause)
                                data.percent[i]['status'] = 'continue';
                            else
                                data.percent[i]['status'] = 'paused';

                            if(data.has_error) {
                                state.has_error = true;
                                data.percent[i]['status'] = 'error';

                                commit(types.MUTATE_TOGLEE_WARNING_MESSAGE, true)
                                

                                if (pause_migration != null)
                                pause_migration.classList.add("disable_button")
                                if (play_migration != null)
                                play_migration.classList.remove("disable_button")
                            }
                        }
                        else
                            data.percent[i]['status'] = '';
                        set_first = true;
                    }

                    if (data.percent[i].finish == 1) {
                        mg_all_done = true
                    }
                    else {
                        mg_all_done = false
                    }
                    Object.assign(mg_data, {
                        [i]: {
                            ...data.percent[i]
                        }
                    })
                }

                commit(types.MUTATE_MIGRATED_DATA, mg_data)
                if (!mg_all_done && !data.pause) {
                    commit(types.MUTATE_TIME_REMAINING, data.time_remaining)
                    dispatch(types.CLICK_START_MIGRATION_PROCESS)
                } else {
                    if (mg_all_done) {
                        commit(types.MUTATE_MG_REPORT, data.report)
                        commit('mutateSelectedComponent', 'app-report')
                        dispatch(types.CLICK_RENDER_LAST_STEP)
                    }
                   
                }
            }).catch(err => {
                commit(types.MUTATE_MIGRATION_FATAL_ERROR, true)
                
                if (pause_migration != null)
                pause_migration.classList.add("disable_button")
                if (play_migration != null)
                play_migration.classList.remove("disable_button")
            })
    },
    [types.CLICK_PAUSE_PROCESS]: ({commit, dispatch}, payload) => {
        let play_migration = document.querySelector('.m_play_migration')
        let pause_migration = document.querySelector('.m_pause_migration')
        let pause_animation = document.querySelector('.migrating_load')

        
        pause_migration.classList.add("disable_button")
        play_migration.classList.add("disable_button")
        
        let formData = new FormData()
        formData.append('action', 'import_process');
        formData.append('ajax', 'true');
        formData.append('status', 'pause');
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
        // .then(response => response.json())
            .then((data) => {
                if (pause_animation != null) {
                    pause_animation.classList.add("stop-animation")
                    pause_migration.classList.add("disable_button")
                    play_migration.classList.remove("disable_button")
                }
                commit(types.MUTATE_MIGRATION_PAUSE, true)
            })
            
    },
    [types.CLICK_MIGRATION_RETURN_BACK]: ({commit, dispatch}, payload) => {
        let formData = new FormData()
        formData.append('ajax', 'true');
        formData.append('action', 'validate_step');
        formData.append('step_number', '3');
        formData.append('to_step_number', '1');
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(response => response.json())
            .then((data) => {
                commit('mutateSelectedComponent', 'app-connection')
            })

    },
    [types.CLICK_RENDER_LAST_STEP]: ({commit, dispatch}, payload) => {
        let formData = new FormData()
        formData.append('ajax', 'true');
        formData.append('action', 'validate_step');
        formData.append('step_number', '3');
        formData.append('to_step_number', '4');
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(response => response.json())
            .then((data) => {
                // commit('mutateSelectedComponent', 'app-connection')
            })
    }

}

export default {
    state,
    getters,
    mutations,
    actions
}
