<template>
    <transition name="modal-fade">
        <div id="mg_modal" v-if="modalVisibility" >
            <div class="mg_modal_content" :style="from_more_options? 'bottom: 50px':'top: 100px;'">
                <div class="mg_modal_p">
                    <div class="mg_modal__close" @click="closeModal(false)"><img src="../../assets/icon-close.png"
                                                                                 alt=""></div>
                    <div class="mg_modal__title">
                        <div class="mg_modal__info">
                            <img src="../../assets/warning-icon.svg" alt=""><h4>{{tr.auto_setting_modal_header}}</h4>
                        </div>
                        <p   v-if="!from_more_options">{{tr.auto_setting_modal_header_description}}</p>
                    </div>
                    <div class="mg_modal__entity">
                        <h4>{{from_more_options? tr.entity_selected:tr.entity_1click}}</h4>
                        <b-row>
                            <b-col>
                                <div class="mg_modal_ec" :class="selected_entities['taxes'] == 0 ?'non-selected':''">
                                   <img   :class="selected_entities['taxes'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['taxes'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.taxes}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['manufactures'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['manufactures'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['manufactures'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.manufactures}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['categories'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['categories'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['categories'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.categories}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['carriers'] == 0 ?'non-selected':''">
                                     <img   :class="selected_entities['carriers'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['carriers'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.carriers}}</span>
                                </div>
                            </b-col>
                            <b-col>
                                <div class="mg_modal_ec" :class="selected_entities['cart_rules'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['cart_rules'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['cart_rules'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.cart_rules}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['orders'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['orders'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['orders'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.orders}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['cms'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['cms'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['cms'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.cms}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['seo'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['seo'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['seo'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.seo}}</span>
                                </div>
                            </b-col>
                            <b-col>
                                <div class="mg_modal_ec" :class="selected_entities['products'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['products'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['products'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.products}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['catalog_price_rules'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['catalog_price_rules'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['catalog_price_rules'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.catalog_price_rules}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['employees'] == 0 ?'non-selected':''">
                                    <img   :class="selected_entities['employees'] == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['employees'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.employees}}</span>
                                </div>
                                <div class="mg_modal_ec" :class="selected_entities['customers'] == 0 ?'non-selected':''">
                                     <img   :class="selected_entities['customers']  == 0 ?'hidden':''" 
                                     src="../../assets/modal-enity-check.svg" alt="">

                                    <span  :class="selected_entities['customers'] != 0 ?'hidden':''" 
                                    class="non-selected-circle"></span>

                                    <span>{{tr.customers}}</span>
                                </div>
                            </b-col>
                        </b-row>
                    </div>
                    <div class="mg_modal__options" v-if="recent_data != 'empty' &&  recent_data=='clear_data'">
                        <h4>
                            {{from_more_options? tr.additional_selected:tr.additional_1click}}
                        </h4>
                        <span class="clear-info"> <img src="../../assets/modal-enity-check.svg" alt="Clear">{{tr.clean_data}}</span>
                        <p>
                            {{tr.clean_data_text}}
                        </p>
                    </div>
                    
                    <div class="mg_modal__options"  v-if="recent_data != 'empty' &&  recent_data=='recent_data'">
                        <h4>
                            {{tr.additional}}
                        </h4>
                        <span class="clear-info"> <img src="../../assets/modal-enity-check.svg" alt="Clear">{{tr.recent_data}}</span>
                        <p>
                           {{tr.recent_data_text}}
                        </p>
                    </div>
                    <div class="mg_modal__options"  v-if="keep_id">
                        <span class="clear-info"> <img src="../../assets/modal-enity-check.svg" alt="Clear">{{tr.keep_id}}</span>
                        <p>
                           {{tr.keep_id_text}}
                        </p>
                    </div>
                </div>
                <div class="modal-error" v-if="fatalErrorSecond"> <img src='../../assets/alert-error.svg'/>{{tr.fatal_error}}</div>
                <div class="mg_modal__button">
                    <button class="mg_modal__more"  v-if="!from_more_options" @click="openMoreOptionsModal"   v-scroll-to="{element: '.more_options__button', duration: 1000, offset: 50 }">{{tr.more_options}}</button>
                    <button class="mg_modal__start"  v-if="from_more_options" @click="migrationStatus('moreOptions')">{{tr.start_migration}}</button>
                    <button class="mg_modal__start"  v-if="!from_more_options" @click="migrationStatus('modal')">{{tr.start_migration}}</button>
                </div>  
            </div>
        </div>
    </transition>

</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from '../../store/type'

    export default {
        name: "modal",
        computed: {
            ...mapGetters({
                checkboxSelectedAdditional: types.CHECKBOX_ADDITIONAL_SELECTED,// get checkbox selection 
                from_more_options: types.MODAL_FROM_MORE_OPTION,// get checkbox selection
                selected_entities: types.MODAL_ENTITIES,// get checkbox selection
                keep_id: types.MODAL_KEEP_ID,// get checkbox selection
                select_all_entity: types.MODAL_SELECT_ALL_ENTITIES,// get checkbox selection
                recent_data: types.MODAL_RECENT_DATA,// get checkbox selection
                modalVisibility: types.MODAL_VISIBILITY,// get modal visibility state
                fatalErrorSecond: types.FATAL_ERROR_SECOND,// get modal visibility state
                tr: 'step_second_trans', // get second step translations
            }),
        },
        destroy:function () {
            this.$store.commit(types.MUTATE_FATAL_ERROR_SECOND, false)
        },
        methods: {
            ...mapActions({
                closeModal: types.CLICK_CLOSE_MODAL,// click close button and close modal
                migrationStatus: types.CLICK_MIGATE, // click Start migration and start mg process
                fatalErrorSecond: types.FATAL_ERROR_SECOND, //get more info component state
                openMoreOptionsModal: types.CLICK_MORE_OPTIONS_MODAL, // click more option button , hide modal and open more options component
            }),

            // check clear data and uncheck migrate recent data
            additionalCheckboxModal(event) {
                if (event == 'clear_data') {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {migrate_recent_data: []})
                }
            }
        }
    }
</script>

<style lang="scss">
    @import "../../scss/main";

    #mg_modal {
        .mg_modal_p {
            padding: 0 30px 20px;
            position: relative;
            border-bottom: 1px solid #eaeef3;
            .mg_modal__entity .row .col {
                padding: 0 0 0 5px!important;
            }
        }
        .mg_modal__title {
            margin-bottom: 25px;
            margin-left: -30px;
            margin-right: -30px;
            background-color: #fdf7e4;
            padding: 20px;
            border-top: 4px solid #eec200;
            border-top-left-radius: 3px;
            border-top-right-radius: 3px;
            box-shadow: 0 1px 3px 0 rgba(63, 63, 68, 0.15), 0 0 0 1px rgba(63, 63, 68, 0.05);
            p {
                font-size: 14px;
                line-height: 1.5;
                margin-bottom: 0px !important;
                padding-left: 38px;
            }
            img {
                float: left;
            }
            .mg_modal__info {
                margin-bottom: 8px;

                h4 {
                    font-weight: 600;
                    display: inline-block;
                    font-size: 18px;
                    vertical-align: bottom;
                    margin: 0 0 0 10px;
                }
            }
        }
        .non-selected {
            // opacity: 0.3;
            color: #bcbfc3;
        }
        .non-selected-circle {
            display: inline-block;
            margin-left: 0 !important;
            border-radius: 50%;
            width: 14px;
            height: 14px;
            background-color: #dbdbdb;
            vertical-align: middle;
        }
        .mg_modal__close {
            position: absolute;
            right: 12px;
            top: 20px;
            img {
                width: 14px !important;
            }
        }
        .mg_modal__button {
            text-align: center;
            padding-bottom: 20px;
            padding-top: 20px;
        }
        .mg_modal__entity {
            margin-bottom: 15px;
            .mg_modal_ec {
                margin-bottom: 15px;
                img {
                    width: 14px !important;
                }
            }
            .mg_modal__ea {
                margin-bottom: 15px;
                border-bottom: solid 0.8px #ececec;
                display: inline-block;
                padding: 0 6px 6px 0;
            }
            h4 {
                font-size: 16px;
                border-bottom: solid 0.8px #ececec;
                padding-bottom: 15px;
            }
            span {
                font-size: 14px;
                margin-left: 10px;
            }
        }
        .mg_modal__options {
            img {
                width: 14px !important;
            }
            h4 {
                font-size: 16px;
                border-bottom: solid 0.8px #ececec;
                padding-bottom: 15px;
            }
            p {
                font-style: normal;
                font-size: 12px;
                color: #212b36;
                margin-left: 25px;
                margin-top: 8px;
            }
            .custom-control-label {

                span {
                    font-size: 11px;
                }
            }
        }//9108
    }

    .modal-error {
        color: #bf0711;
        font-size: 14px;
        font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
        margin-left: 30px;
        margin-top: 6px;
    }

    .clear-info {
        font-size: 14px;
         color: #212b36;
         vertical-align: middle;
         img {
             margin-right: 10px;
         }
    }

    #app button.mg_modal__more, 
    #app button.mg_modal__start {
        padding: 8px 15px !important;
        font-size: 14px !important;
    }
</style>
