import {store} from '../store'
import * as types from '../type'

const state = {
    configurationError: false,
    backToConnection: true,
    moreInfo: false,
    configurationErrorMessage: "",
    process_entity: [],
    fatalErrorSecond: false,

}
const getters = {
    [types.MIGRATION_STATUS]: (state) => {
        return state.configuration;
    },
    [types.MORE_INFO]: (state) => {
        return state.moreInfo;
    },
    [types.CONFIGURATION_ERROR]: (state) => {
        return state.configurationErrorMessage;
    },
    [types.CONFIGURATION_ERROR_STATUS]: (state) => {
        return state.configurationError;
    },
    [types.FATAL_ERROR_SECOND]: (state) => {
        return state.fatalErrorSecond;
    },
    [types.PROCESS_ENTITY]: (state) => {
        return state.process_entity;
    },
}

const mutations = {
    [types.MUTATE_MIGRATION_STATUS]: (state, payload) => {
        state.configuration = payload;
    },
    [types.MUTATE_LEARN_MORE]: (state, payload) => {
        state.moreInfo = payload;
    },
    [types.MUTATE_CONFIGUTATION_ERROR]: (state, payload) => {
        state.configurationError = payload;
    },
    [types.MUTATE_CONFIGURATOIN_ERROR_MESSAGE]: (state, payload) => {
        state.configurationErrorMessage = payload;
    },
    [types.MUTATE_FETCH_PROCESS_ENTITY]: (state, payload) => {
        state.process_entity = payload;
    },
    [types.MUTATE_FATAL_ERROR_SECOND]: (state, payload) => {
        state.fatalErrorSecond = payload;
    }
}

const actions = {
    [types.CLICK_MIGATE]: ({commit, state, dispatch, rootState}, clickLocation) => {

        let loading_trans = store.getters.loading_trans;
        commit(types.MUTATE_OPEN_MODAL, false);

        commit(types.MUTATE_LOADING_HEADER, loading_trans.second_step_header);
        commit(types.MUTATE_LOADING_DESCRIPTION, loading_trans.second_step_description1);
        commit(types.MUTATE_LOADING, true);

        commit(types.MUTATE_FATAL_ERROR_SECOND, false)
        let formData = new FormData()
        for (var i in rootState.moreOptions.selected) {
            for (var j in rootState.moreOptions.selected[i]) {
                formData.append('map[' + i + '][' + JSON.parse(rootState.moreOptions.selected[i][j])['source_id'] + ']', 
                JSON.parse(rootState.moreOptions.selected[i][j])['target_id']);
            }
        }

        switch (clickLocation) {
            case 'moreOptions':
                formData.append('auto_map', false)
                formData.append('reset', true)
                break;
            case 'modal':
                formData.append('auto_map', true)
                formData.append('reset', true)
                break;
            case 'contiune':
                formData.append('reset', false)
                break;
        }
        for (var i in rootState.moreOptions.checkbox_selected) {
            switch (clickLocation) {
                case 'moreOptions':

                    if (rootState.moreOptions.checkbox_selected[i] == 0) {
                        formData.append('entity[' + i + '][0]', '0')
                    }
                    else {
                        formData.append('entity[' + i + '][0]', '1')
                    }
                    break;
                case 'modal':
                    formData.append('entity[' + i + '][0]', '1')
                    break;
            }
        }

        for (var additional in rootState.moreOptions.checkbox_additional_selected) {
            if (rootState.moreOptions.checkbox_additional_selected[additional] == 0) {
                formData.append('additional[' + additional + '][0]', '0')
            }
            else {
                formData.append('additional[' + additional + '][0]', '1')
            }
        }

        formData.append('speed', rootState.moreOptions.speed);
        formData.append('ajax', 'true');
        formData.append('action', 'validate_step');
        formData.append('step_number', '2');
        formData.append('to_step_number', '3');
        
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(response => {
                    if (response.ok) {
                        return response.json();
                    } else {
                    commit(types.MUTATE_LOADING, false);
                    commit(types.MUTATE_OPEN_MODAL, true);
                        commit(types.MUTATE_FATAL_ERROR_SECOND, true)
                        throw new Error('Something went wrong');
                    }
                } 
            )
            .then((data) => {
                let data_migrate = {}
                if (!data.has_error && !data.haswarning) {
                    dispatch(types.CLICK_CLOSE_MODAL, false)
                    let set_first = false;
                    for (let i in data.step_form) {
                        data.step_form[i]['type'] = data.step_form[i]['type']; 


                        if(data.step_form[i]['finish'] == 1)
                            data.step_form[i]['status'] = 'finished';
                        else
                        {
                            if(!set_first)
                                data.step_form[i]['status'] = 'paused';
                            else
                                data.step_form[i]['status'] = '';
                        
                            set_first = true;
                        }

                        // data.step_form[i]['status'] = '';

                        Object.assign(data_migrate, {
                            [data.step_form[i].type]: {
                                ...data.step_form[i]
                            }
                        })
                    }
                    commit(types.MUTATE_MIGRATED_DATA, data_migrate)
                    commit('mutateSelectedComponent', 'app-migration')
                    // Auto start migration
                    commit(types.MUTATE_MIGRATION_PAUSE, false)
                    dispatch(types.CLICK_START_MIGRATION_PROCESS,'contiune')     
                    commit(types.MUTATE_LOADING, false);

                  
                }
                if (data.has_error) {
                    if(clickLocation == "modal")
                        commit(types.MUTATE_OPEN_MODAL, true);

                    commit(types.MUTATE_CONFIGURATOIN_ERROR_MESSAGE, data.errors)
                    commit(types.MUTATE_LOADING, false);
                    commit(types.MUTATE_CONFIGUTATION_ERROR, true)
                }
                else {
                    commit(types.MUTATE_CONFIGUTATION_ERROR, false)
                }
            })
    },

    [types.CLICK_BACK_CONNECTION]: ({commit}) => {
        let formData = new FormData()
        formData.append('ajax', 'true');
        formData.append('action', 'validate_step');
        formData.append('step_number', '2');
        formData.append('to_step_number', '1');
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(response => response.json())
            .then((data) => {
                commit(types.MUTATE_FATAL_ERROR_SECOND, false)
                commit('mutateSelectedComponent', 'app-connection')
            })
        // if (state.backToConnection) {
        // }
    },
    [types.CLICK_LEARN_MORE]: ({commit}, payload) => {
        commit(types.MUTATE_LEARN_MORE, payload)
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
