import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as mutations from './mutations'
import * as getters from './getters'
import moreOptions from './modules/moreOptions'
import modal from './modules/modal'
import configuration from './modules/configurations'
import connection from './modules/connection'
import migration from './modules/migration'
import migrationReport from './modules/migrationReport'
import loading from './modules/loading'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state:{
      selectedComponent:'',
      step_first_trans:translate.step_1,
      step_second_trans:translate.step_2,
      step_third_trans:translate.step_3,
      step_fourth_trans:translate.step_4,
      loading_trans: translate.loading,
      header_translate_labels:translate.header,
      attention_labels: translate.attention,
  },
  getters,
  mutations,
  actions,
  modules:{
    namespaced: true,
    configuration,
    moreOptions,
    modal,
    connection,
    migration,
    migrationReport,
    loading
  }
})
