<template>
    <div class="module_head__title">
        <!--<app-loader/>-->
        <h2>{{tr.header}}</h2>
        <p>{{tr.header_description}}</p>
        <div class="migration">
            <div class="migration_info">
                <div class="migration_icon">
                    <img src="../../assets/info_icon.svg" alt="">
                </div>
                <div class="migration_text">
                    <p>{{tr.step_title}}</p>
                    <p>{{tr.step_description}}</p>
                    <div class="migration_info__support">
                        <ul>
                            <li><a href="#" @click.prevent="startMigration('contiune')"
                            v-bind:class="migrationPause == false ? 'm_play_migration disable_button': 'm_play_migration'"
                            ><img
                                    src="../../assets/shapePlay.svg" alt=""><span>{{tr.play}}</span></a></li>
                            <li><a href="#" @click.prevent="pauseMigration"
                                v-bind:class="migrationPause == true ? 'm_pause_migration disable_button': 'm_pause_migration'"
                                ><img src="../../assets/button-pause.svg"
                                                                                 alt=""><span>{{tr.pause}}</span></a>
                            </li>
                            <li><a href="#" @click.prevent="openAttentionModal(true)"   v-scroll-to="{element: '#app', duration: 1000, offset: -100 }"><img
                                    src="../../assets/button-stop.svg" alt=""><span>{{tr.stop}}</span></a>
                            </li>
                            <li><img src="../../assets/is-approximately-equal-to.svg"
                                                 alt=""><span>{{timeRemaining}}</span></li>
                        </ul>
                        <!-- <div class="migration_fatal">{{migrationFatalError ? tr.fatal_error : ""}}</div> -->
                    </div>
                </div>
            </div>
            <div class="migration_progress">
                <h4>{{tr.migration_status}}</h4>
                <div class="mg__bottom_space" v-for="(migrateData,index) in migrateDatas">
                    <b-row>
                        <b-col cols="10" md="10" sm="11" xs="11">
                            <div class="d-flex justify-content-between mg_count">
                                <div>{{entity_names[migrateData.type]}}</div>
                                <div>
                                    <span class="precent_border">{{parseInt((migrateData.imported / migrateData.total) * 100)}}%</span>
                                    <span class="mg_counter__holder">{{migrateData.imported}}/{{migrateData.total}}</span>
                                </div>
                            </div>
                            <b-progress :value="(migrateData.imported/migrateData.total)*100" height="4px" width="486px"
                                        v-bind:variant="migrateData.status=='finished' ? 'success' : migrateData.status=='continue' && migrationFatalError ?
                                         'warning' : 'primary' "></b-progress>
                            
                              <div class="warning_message"  v-if="migrateData.status=='continue' && migrationFatalError" v-else-if="migrateData.status=='error'" >
                                <div class="d-flex justify-content-between warning_message__title">
                                 <span>{{tr.error_message_header}}</span>
                                <!-- <div @click="clickToglleWarning(false)"><img src="../../assets/close_icon.svg" alt=""></div> -->
                                </div>
                                <div class="warning_message__content">{{tr.fatal_error}}
                                <!-- <p v-for="error in ToglleWarningMessage">
                                    {{error}}
                                </p>

                                -->
                                </div>
                            </div> 

                        </b-col>
                        <b-col cols="2" md="1" sm="1" xs="1" class="progress-icon">
                            <img v-if="migrateData.status=='finished'" src="../../assets/check.svg"
                                 :key="migrateData.migrationSuccess"
                                 alt="">

                            <img v-if="migrateData.status=='error'" src="../../assets/error-icon.svg"
                                 :key="migrateData.migrationSuccess" alt="" class="progress-warning">

                            <img v-if="migrateData.status=='continue' && migrationFatalError" src="../../assets/error-icon.svg"
                                 :key="migrateData.migrationSuccess" alt="" class="progress-warning">

                            <img v-if="migrateData.status=='paused'" src="../../assets/pause_icon.svg"
                                 :key="migrateData.migrationSuccess" alt="" class="progress-pause">

                            <div v-if="migrateData.status == 'continue'  && !migrationFatalError" 
                            class='typing_loader'></div>
                        </b-col>
                    </b-row>
                </div>
            </div>
        </div>
        <app-attention-modal/>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapMutations} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from '../../store/type'
    import warningMessage from '@/components/components/warningMessage'
    import LoaderLastStep from '@/components/components/LoaderLastStep' 
    import ModalAttention from '@/components/components/AttentionModal'

    import Vue from 'vue' 

    Vue.component('app-warning-message', warningMessage)
    Vue.component('app-loader', LoaderLastStep)
    Vue.component('app-attention-modal', ModalAttention)

    export default {
        name: "migration",

        destroyed() {
//            hide attention modal(when  clicked stop migration)  when component destroy
            this.$store.commit(types.MUTATE_CLOSE_ATTETION_MODAL, false)
            this.$store.commit(types.MUTATE_MIGRATION_FATAL_ERROR, false)
        },

        methods: {
            ...mapActions({
                startMigration: types.CLICK_START_MIGRATION_PROCESS, //click play button and start migration
                pauseMigration: types.CLICK_PAUSE_PROCESS, // click pause button and pause migration prosess
                reutrnBackConnection: types.CLICK_MIGRATION_RETURN_BACK, // click stop migration and return first step (app-connection)
            }),

            ...mapMutations({
                openAttentionModal: types.MUTATE_OPEN_ATTETION_MODAL, //click stop button and show attention modal
//                closeAttentionModal: types.MUTATE_CLOSE_ATTETION_MODAL,
            }),
        },

        computed: {
            ...mapGetters({
                migrateDatas: types.MIGRATED_DATA, // get entity for migrate
                toggleWarning: types.TOGGLE_WARNING, //
                timeRemaining: types.TIME_REMAINING, //
                migrationPause: types.MIGRATION_PAUSE, //
                migrationFatalError: types.MIGRATION_FATAL_ERROR, //
                tr: 'step_third_trans',
                entity_names: 'step_second_trans', // get tranlation for third step (app-migration)
            }),
        }

    }

</script>

<style lang="scss">
    @import "../../scss/main";

    .migration {
        .migration_fatal {
            color: red;
            font-style: italic;
            font-size: 11px;
            font-weight: 300;
            line-height: 1.73;
        }
        .migration_info {
            padding: 22px 100px 22px 52px !important;
            background: white;
        }
        .migration_progress {
                padding: 20px 24px 20px 52px !important;
            h4 {
                margin-top: 0;
                margin-bottom: 18px;
                font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
                font-size: 16px;
                color: #212b36;
                font-weight: 400;
            }
            .stop-animation {
                animation-play-state: paused;
            }
            .progress-bar {
                min-width: 5px !important;
            }
            .bg-primary {
                background: #5c6ac4 !important;
            }
            .bg-success {
                background-color: #5fdcb8 !important;
            }
            .bg-warning {
                background-color: #de3618 !important;
            }
            .mg__bottom_space {
                margin-bottom: 24px;
            }
            padding: 28px 46px 20px 46px;
            background: #f5f6f7;
            .mg_count {
                margin-bottom: 5px;
                width: 486px;
                & > div {
                    font-size: 14px;
                    color: #7f8fa4;
                    .precent_border {
                        position: relative;
                        border-radius: 1px;
                        padding-right: 12px;
                        border-right: 1px solid #000;
                        // &:before {
                        //     content: "";
                        //     width: 2px;
                        //     height: 14px;
                        //     background: #000;
                        //     position: absolute;
                        //     top: 0;
                        //     right: 0;
                        // }
                    }
                    .mg_counter__holder {
                        padding-left: 10px;
                    }
                }
            }
            .progress {
                margin-bottom: 0 !important;
            }
        }
    }


.progress-icon img {
    margin-left: 40px;
    margin-top: 15px;
}

.progress-icon div {
    margin-top: 15px;
    margin-left: 40px;
    margin-bottom: -15px;
}

.migration_info__support ul li:last-child {
    margin-right: 38px !important;
}

.progress-warning {
    margin-left: 34px !important;
}
.progress-pause{
    margin-left: 34px !important;
    margin-top: 2px !important;

}

  .warning_message {
    width: 486px !important;
    border: solid 1px #c4cdd5;
    border-radius: 8px;
    padding: 15px;
    position: relative;
    margin-top: 20px;
    background-color: #fbeae5;
    &:after{
      content: "";
      width: 20px;
      height: 20px;
      top: -11px;
      left: 10%;
      position: absolute;
      border-left: solid 1px #c4cdd5;
      border-top: solid 1px #c4cdd5;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      background-color: #fbeae5;
      z-index: 1;
    }
    .warning_message__title {
      margin-bottom: 10px;
      span {
        font-size: 16px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.5;
        letter-spacing: normal;
        color: #212b36;
      }
    }
    .warning_message__content {
        color: #4d4f5c;
        font-size: 12px;
    }
  }
  
  .progress {
      width: 486px;
  }

  .migration_icon {
      margin-right: 24px !important;
  }

  #app .migration .migration_text p {
    margin: 0 0 18px!important;
}

   .migration_text p:first-child {
        margin: 0 0 18px!important;
   }

   .migration_info__support ul li:last-child {
       margin-right: 0 !important;
   }

</style>
