<template>
    <div id="app">
        <SectionHeader/>
        <SectionBody/>
        <app-loading></app-loading>

    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from './store/type'

    import Vue from 'vue'
    import SectionBody from '@/components/section_components/SectionBody'
    import SectionHeader from '@/components/section_components/SectionHeader'
    import Loading from '@/components/components/Loading'
    import Modal from '@/components/main_components/Modal'

    Vue.component('app-loading', Loading)
    Vue.component('app-modal', Modal)

    export default {
        name: 'App',
        beforeCreate: function () {
            if (!step_status || step_status == 1) {
                this.$store.dispatch('changeComponent', 'app-connection')
            }
            if (step_status == 2) {
                this.$store.dispatch('changeComponent', 'app-configuration')
                this.$store.dispatch(types.UPDATE_MIDDLE_STATES, current_step, {root: true})
            }
            if (step_status == 3) {
                this.$store.dispatch('changeComponent', 'app-migration')
                this.$store.dispatch(types.UPDATE_MIGRATION_STATES, current_step, {root: true})
            }
            if (step_status == 4) {
                this.$store.commit('mutateSelectedComponent', 'app-report')
                this.$store.commit('migrationReport/MUTATE_MG_REPORT',current_step)
            }
        },
        components: {
            SectionBody,
            SectionHeader,
        },
        methods: {
            ...mapActions({
                connectionStatusClick: types.CLICK_CONNECTION_STATUS,
                changeComponent: 'changeComponent'
            }),
        },
    }
</script>

<style lang="scss" scoped>
    @import "scss/main.scss";

    #app {
        padding-bottom: 100px;
    }
</style>
