import * as types from '../type'

const state = {
    show: false,
    entities: {},
    recent_data: false,
    keep_id: false,
    select_all_entity: false,
    from_more_option: false,
    
}
const getters = {
    [types.MODAL_VISIBILITY]: (state, getters, rootGetters) => {

        return state.show;
    },
    [types.MODAL_ENTITIES]: (state) => {

        return state.entities;
    },
    [types.MODAL_RECENT_DATA]: (state) => {

        return state.recent_data;
    },
    [types.MODAL_SELECT_ALL_ENTITIES]: (state) => {

        return state.select_all_entity;
    },
    [types.MODAL_FROM_MORE_OPTION]: (state) => {

        return state.from_more_option;
    },
    [types.MODAL_KEEP_ID]: (state) => {

        return state.keep_id;
    },
}
//MODAL_ENTITIES
//MODAL_RECENT_DATA
const mutations = {
    [types.MUTATE_CLOSE_MODAL]: (state, payload) => {
        state.show = payload;
    },
    [types.MUTATE_OPEN_MODAL]: (state, payload) => {
        state.show = payload;
    },
    [types.MUTATE_MODAL_ENTITIES]: (state, payload) => {
        state.entities = payload;
    },
    [types.MUTATE_MODAL_RECENT_DATA]: (state, payload) => {
        state.recent_data = payload;
    },
    [types.MUTATE_MODAL_SELECT_ALL_ENTITIES]: (state, payload) => {
        state.select_all_entity = payload;
    },
    [types.MUTATE_MODAL_FROM_MORE_OPTION]: (state, payload) => {
        state.from_more_option = payload;
    },
    [types.MUTATE_MODAL_KEEP_ID]: (state, payload) => {
        state.keep_id = payload;
    }
}

const actions = {
    [types.CLICK_OPEN_MODAL]: ({commit, rootState}, payload) => {
        commit(types.MUTATE_MODAL_FROM_MORE_OPTION, payload);


        if(!payload){
            commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {migrate_recent_data: []});
            commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {keep_id: ['keep_id']});
            commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {clear_data: ['clear_data']});
            

            rootState.moreOptions.select_all_entity = true;
            let selected_entity = {};
            for (let i in rootState.moreOptions.checkbox_selected) { 
                    Object.assign(selected_entity, {[i]: [i]});
            }
            commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, selected_entity);
        }


        commit(types.MUTATE_MODAL_ENTITIES, rootState.moreOptions.checkbox_selected);
        commit(types.MUTATE_MODAL_SELECT_ALL_ENTITIES, rootState.moreOptions.select_all_entity);

        if(rootState.moreOptions.checkbox_additional_selected['migrate_recent_data'].length == 0 && rootState.moreOptions.checkbox_additional_selected['clear_data'].length == 0)
        {
            commit(types.MUTATE_MODAL_RECENT_DATA, 'empty');
        }
        else
        {
            if(rootState.moreOptions.checkbox_additional_selected['migrate_recent_data'].length == 0) {
                commit(types.MUTATE_MODAL_RECENT_DATA, 'clear_data');
            }
            else {
                commit(types.MUTATE_MODAL_RECENT_DATA, 'recent_data');
            }
        }

        // Keep Id
        if(rootState.moreOptions.checkbox_additional_selected['keep_id'].length == 0) {
            commit(types.MUTATE_MODAL_KEEP_ID, false);
        }
        else {
            commit(types.MUTATE_MODAL_KEEP_ID, true);
        }

        commit(types.MUTATE_OPEN_MODAL, true);
    },

    [types.CLICK_CLOSE_MODAL]: ({commit}, payload) => {
        commit(types.MUTATE_CLOSE_MODAL, payload)
    },
    [types.CLICK_MORE_OPTIONS_MODAL]: ({commit,rootState}, payload) => {
        var promise = new Promise(function (resolve, reject) {
            setTimeout(
                function () {
                    commit(types.MUTATE_CLOSE_MODAL, false)
                    resolve()
                }, 300
            )

        });
        promise.then(function () {
            rootState.moreOptions.toggle_more_options=true
            // commit('mutateSelectedComponent', 'app-migration')
        })
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
