import * as types from '../type'

const state = {
    report: [],
    fatalErrorReport:false
}
const getters = {
    [types.MG_REPORT]: (state) => {
        return state.report;
    },
    [types.MG_REPORT_FATAL]: (state) => {
        return state.fatalErrorReport;
    },
}

const mutations = {
    [types.MUTATE_MG_REPORT]: (state, report) => {
        state.report = report;
    },
    [types.MUTATE_MIGRATION_REPORT_FATAL_ERROR]: (state, payload) => {
        state.fatalErrorReport = payload;
    },
}

const actions = {
    [types.CLICK_RETURN_BACK]: ({state, commit}) => {
        let formData = new FormData()
        formData.append('ajax', 'true');
        formData.append('action', 'validate_step');
        formData.append('step_number', '4');
        formData.append('to_step_number', '1');
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    commit(types.MUTATE_MIGRATION_REPORT_FATAL_ERROR, true)
                    throw new Error('Something went wrong');
                }
            })
            .then((data) => {
                commit('mutateSelectedComponent', 'app-connection')
            })
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
