<template>
    <div class="configuration_ranger">
        <div class="d-flex justify-content-between configuration_speed__number">
            <span>5</span><span>10</span><span>20</span><span>30</span><span>40</span><span>50</span><span>60</span><span>70</span><span>80</span><span>90</span><span>100</span>
        </div>
        <div class="configuration_ranger__conentent">
            <div class="d-flex">
                <input id="input-ranger" type="range" @input="changeRanger" step="10" v-model="rangerValue">
            </div>
        </div>
        <div class="d-flex justify-content-between configuration_speed">
            <span>{{tr.low}}</span><span>{{tr.medium}}</span><span>{{tr.high}}</span>
        </div>
        <div class="configuration_speed__info">
            <span>{{tr.sped_text}}</span>
        </div>
        {{rangerValue}}
    </div>
</template>
<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from '../../store/type'

    export default {
        name: "ranger",
        mounted: function () {
            this.createElement() //method will execute at pageload
        },
        beforeDestroy: function () {
            this.removeElement() //remove 'style' tag when component destoryed
        },

        //        get data from store
        computed: {
            ...mapGetters({
                tr: 'step_second_trans', // translation for Ranger
            }),

            rangerValue: { //this method get method value and set it to store(chanege value with dispatch)
                get() {
                    return this.$store.getters.speed;
                },
                set(value) {
                    this.$store.dispatch(types.CHANGE_RANGER_SPEED_VALUE, value);
                }
            }
        },

        methods: {
//            changeRanger method add style tag for ranger style(background-size etc.) when component mounted
            changeRanger: (event) => {
                let choremBg = "input[type=range]::-webkit-slider-runnable-track {background-size: " + event.target.value + "% 100%!important;}"
                let moz_bg = "input[type=range]::-moz-range-track {background-size: " + event.target.value + "% 100%!important;}"
                let style_tag = document.querySelector("style:last-child")
                style_tag.innerHTML = ""
                style_tag.appendChild(document.createTextNode(choremBg))
                style_tag.appendChild(document.createTextNode(moz_bg))
            },

//            createElement method create style tag and append it to 'head' tag
            createElement: () => {
                let rangerStyleTag = document.createElement('style');
                document.head.appendChild(rangerStyleTag);
            },

            //            removeElement method remove style tag from 'head' tag when component destroy
            removeElement: () => {
                let style_tag = document.querySelector("style:last-child")
                style_tag.remove()
            }
        }
    }
</script>

<style lang="scss" scoped>
    .configuration_ranger {
        flex: 2 0 210px;
        .configuration_speed__number {
            font-size: 12px;
            margin-bottom: 5px;
            padding-left: 13px;
        }
        .configuration_speed {
            font-size: 12px;
            color: #a5a6b5;
            margin-top: 5px;
        }
        .configuration_speed__info {
            margin-top: 10px;
            line-height: 1.2;
            span {
                font-size: 12px;
                color: #212b36;
            }
        }
        .configuration_ranger__conentent {

            height: 12px;
            background: white;
            padding: 0 5px 0 5px;
            input[type=range] {
                -webkit-appearance: none;
                margin: 5px 0;
                width: 100%;
            }
            input[type=range]:focus {
                outline: none;
            }
            input[type=range]::-webkit-slider-runnable-track {
                /*width: 200px;*/
                height: 4px;
                background: linear-gradient(to right, #5C6AC4, #5C6AC4), white;
                background-size: 50% 100%;
                background-repeat: no-repeat;
                border-radius: 5px;
            }
            input[type=range]::-webkit-slider-thumb {
                box-shadow: 0 1px 2px 0 rgba(171, 175, 179, 0.6), inset 0 -2px 5px 0 rgba(122, 125, 128, 0.2);
                border: solid 0.8px rgba(122, 125, 128, 0.3);
                height: 23px;
                width: 11px;
                border-radius: 5px;
                background: #FFFFFF;
                cursor: pointer;
                -webkit-appearance: none;
                margin-top: -11px;
            }
            /*input[type=range]:focus::-webkit-slider-runnable-track {*/
            /*background: #3071A9;*/
            /*}*/
            input[type=range]::-moz-range-track {
                height: 4px;
                background: linear-gradient(to right, #5C6AC4, #5C6AC4), white;
                background-size: 50% 100%;
                background-repeat: no-repeat;
                border-radius: 5px;
            }
            input[type=range]::-moz-range-thumb {
                box-shadow: 0 1px 2px 0 rgba(171, 175, 179, 0.6), inset 0 -2px 5px 0 rgba(122, 125, 128, 0.2);
                border: solid 0.8px rgba(122, 125, 128, 0.3);
                height: 23px;
                width: 11px;
                border-radius: 5px;
                background: #FFFFFF;
                cursor: pointer;
                -webkit-appearance: none;
                margin-top: -11px;
            }
            input[type=range]::-ms-track {
                width: 100%;
                height: 10px;
                cursor: pointer;
                animate: 0.2s;
                background: transparent;
                border-color: transparent;
                color: transparent;
            }
            input[type=range]::-ms-fill-lower {
                background: #5c6ac4;
                border: 1px solid #000000;
                border-radius: 10px;
                box-shadow: 1px 1px 1px #000000;
            }
            input[type=range]::-ms-fill-upper {
                background: #5c6ac4;
                border: 1px solid #000000;
                border-radius: 10px;
                box-shadow: 1px 1px 1px #000000;
            }
            input[type=range]::-ms-thumb {
                box-shadow: 1px 1px 1px #000000;
                border: 1px solid #000000;
                height: 30px;
                width: 15px;
                border-radius: 5px;
                background: #FFFFFF;
                cursor: pointer;
            }
        }
    }
</style>
