import * as types from '../type'
import {store} from '../store'

const state = {
    multi_shops: {},
    currencies: {},
    order_states: {},
    languages: {},
    customer_groups: {},
    selected: {
        multi_shops: {},
        currencies: {},
        languages: {},
        order_states: {},
        customer_groups: {}
    },
    mg_data: [
        {text: 'Taxes', value: 'taxes'},
        {text: 'Manufactures', value: 'manufactures'},
        {text: 'Categories', value: 'categories'},
        {text: 'Carriers', value: 'carriers'},
        {text: 'Cart rules', value: 'cart_rules'},
        {text: 'Orders', value: 'orders'},
        {text: 'CMS', value: 'cms'},
        {text: 'SEO', value: 'seo'},
        {text: 'Products', value: 'products'},
        {text: 'Catalog rules', value: 'catalog_price_rules'},
        {text: 'Employees', value: 'employees'},
        {text: 'Customers', value: 'customers'},
        {text: 'Accessories', value: 'accessories'},
        {text: 'Message threads', value: 'message_threads'},
    ],
    mg_data_additional: [
        {text:  'Keep ID' , value: 'keep_id'},
        {text:  'Recent data migration', value: 'migrate_recent_data'},
        {text:  'Clear current data', value: 'clear_data'},
    ],
    checkbox_selected: {
        taxes: ['taxes'],
        manufactures: ['manufactures'],
        categories: ['categories'],
        carriers: ['carriers'], 
        cart_rules: ['cart_rules'],
        orders: ['orders'],
        cms: ['cms'],
        seo: ['seo'],
        products: ['products'],
        employees: ['employees'],
        customers: ['customers'],
        catalog_price_rules: ['catalog_price_rules'],
        message_threads: ['message_threads'],
        accessories: ['accessories'],
    },
    checkbox_additional_selected: {
        clear_data: ['clear_data'],
        migrate_recent_data: ['migrate_recent_data'],
        keep_id: ['keep_id'],
    },
    speed: 50,
    toggle_more_options: false,
    select_all_entity: false,
}

const getters = {
    [types.ORDER_STATE]: (state) => {
        return state.order_states;
    },
    [types.MULTI_STORE]: state => {
        return state.multi_shops
    },
    [types.CURRENCIES]: state => {
        return state.currencies
    },
    [types.LANGUAGE]: state => {
        return state.languages
    },
    [types.CUSTOMER_GROUP]: state => {
        return state.customer_groups
    },
    [types.MG_DATA]: state => {

        // Set server Prestashop Translate texts
        state.mg_data[0]['text'] = store.getters.step_second_trans.taxes;
    
        state.mg_data[1]['text'] = store.getters.step_second_trans.manufactures;
    
        state.mg_data[2]['text'] = store.getters.step_second_trans.categories;
    
        state.mg_data[3]['text'] = store.getters.step_second_trans.carriers;
    
        state.mg_data[4]['text'] = store.getters.step_second_trans.cart_rules;
    
        state.mg_data[5]['text'] = store.getters.step_second_trans.orders;

        state.mg_data[6]['text'] = store.getters.step_second_trans.cms;

        state.mg_data[7]['text'] = store.getters.step_second_trans.seo;
    
        state.mg_data[8]['text'] = store.getters.step_second_trans.products;
    
        state.mg_data[9]['text'] = store.getters.step_second_trans.catalog_price_rules;
    
        state.mg_data[10]['text'] = store.getters.step_second_trans.employees;
    
        state.mg_data[11]['text'] = store.getters.step_second_trans.customers;

        state.mg_data[13]['text'] = store.getters.step_second_trans.message_threads;

        state.mg_data[12]['text'] = store.getters.step_second_trans.accessories;


        return state.mg_data
    },
    [types.MG_ADDITIONAL_DATA]: state => {

        if(state.mg_data_additional[0]['text'] == null)
            state.mg_data_additional[0]['text'] = store.getters.step_second_trans.keep_id;
        
        if(state.mg_data_additional[1]['text'] == null)
            state.mg_data_additional[1]['text'] = store.getters.step_second_trans.recent_data;
        
        if(state.mg_data_additional[2]['text'] == null)
            state.mg_data_additional[2]['text'] = store.getters.step_second_trans.clear_data;

        return state.mg_data_additional
    },
    [types.CHECKBOX_ADDITIONAL_SELECTED]: state => {
        return state.checkbox_additional_selected
    },
    [types.CHECKBOX_SELECTED]: state => {
        return state.checkbox_selected
    },
    [types.TOGGLE_MORE_OPTIONS]: state => {
        return state.toggle_more_options
    },
    [types.SELECTED_OPTION]: state => {
        return state.selected
    },
    [types.SELECTED_RADIO]: state => {
        return state.radio_selected
    },
    [types.MIGRATION_SPEED]: state => {
        return state.speed
    },
    [types.SELECT_ALL_ENTITY]: state => {
        return state.select_all_entity
    },

}
const mutations = {
    [types.MUTATE_TOGGLE_OPTIONS]: state => {
        state.toggle_more_options = !state.toggle_more_options;
    },
    [types.MUTATE_SELECTED_RADIO]: (state, payload) => {
        state.radio_selected = payload;
    },
    [types.MUTATE_FETCH_UPDATE_OBJECTS]: (state, payload) => {
        Object.assign(state, payload)
    },
    [types.MUTATE_FETCH_SELECTED_OBJECT]: (state, payload) => {
        Object.assign(state.selected, payload)
    },
    [types.MUTATE_SELECTED_FORM_DATA]: (state, payload) => {
    },
    [types.MUTATE_SPEED_VALUE]: (state, speed) => {
        if(speed==0){
            state.speed = 5
        }
        else{
            state.speed = speed
        }

    },
    [types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT]: (state, entity_selected) => {
        let updateObjEntity = {...state.checkbox_selected,...entity_selected}
        state.checkbox_selected = updateObjEntity
    },
    [types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT]: (state, additional_selected) => {
        let updateObjAdditional = {...state.checkbox_additional_selected,...additional_selected}
        state.checkbox_additional_selected = updateObjAdditional
    },

    [types.MUTATE_CHANGE_ALL_SELECTED]: (state, select_all_entity) => {
        state.select_all_entity = select_all_entity
    },
}
const actions = {
    [types.CLICK_TOGGLE_OPTIONS]: ({commit}) => {
        commit(types.MUTATE_TOGGLE_OPTIONS)
    },
    [types.FETCH_UPDATE_OBJECTS]: ({commit}, payload) => {

        // commit(types.MUTATE_LOADING_HEADER, loading_trans.first_step_header);
        // commit(types.MUTATE_LOADING_DESCRIPTION, loading_trans.first_step_description);
        commit(types.MUTATE_LOADING, false);
        commit(types.MUTATE_FETCH_UPDATE_OBJECTS, payload)


    },

    [types.CHANGE_RANGER_SPEED_VALUE]: ({commit}, speed) => {
        commit(types.MUTATE_SPEED_VALUE, speed)
    },

    [types.CLICK_SELECT_ALL_ENTITY]: ({state, commit}, checked) => {
        let selected_entity = {}
        for (let i in state.checkbox_selected) {
            if (checked) {
                Object.assign(selected_entity, {[i]: [i]})
            }
            else {
                Object.assign(selected_entity, {[i]: []})
            }
        }
        commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, selected_entity)
    },
    [types.CHANGE_ALL_SELECTED_ENTITY]: ({state, commit}, checked) => {
        let all_selected_entity = false
        setTimeout(() => {
            for (let selected in state.checkbox_selected) {
                if (state.checkbox_selected[selected].length == 0) {
                    all_selected_entity = false
                    break
                }
                else {
                    all_selected_entity = true
                }
            }
            if (all_selected_entity) {
                commit(types.MUTATE_CHANGE_ALL_SELECTED, true)
            } else {
                commit(types.MUTATE_CHANGE_ALL_SELECTED, false)
            }
        }, 5)
    },
}
export default {
    state,
    getters,
    mutations,
    actions
}
