import {store} from '../store'
import * as types from '../type'

const state = {
    regCheck: null,
    connectionStatus: true,
    sourceShopUrl: source_url,
    connectionErrorMessage: "",
    fatalError: false,
    show_loading: false,
}
const getters = {
    [types.REG_STATUS]: (state) => {
        return state.regCheck;
    },
    [types.CONNECTION_STATUS]: (state) => {
        return state.connectionStatus;
    },
    [types.SOURCE_SHOP_URL]: (state) => {
        return state.sourceShopUrl;
    },
    [types.CONNECTION_ERROR_MESSAGE]: (state) => {
        return state.connectionErrorMessage;
    },
    [types.CONNECTION_FATAL_ERROR_MESSAGE]: (state) => {
        return state.fatalError;
    },
}

const mutations = {
    [types.MUTATE_REG_STATUS]: (state, payload) => {
        state.regCheck = payload;
    },
    [types.MUTATE_CONNECTION_STATUS]: (state, payload) => {
        state.connectionStatus = payload;
    },
    [types.MUTATE_SOURCE_URL]: (state, payload) => {
        state.sourceShopUrl = payload;
    },
    [types.MUTATE_CONNECTION_ERROR_MESSAGE]: (state, payload) => {
        state.connectionErrorMessage = payload;
    },
    [types.MUTATE_CONNECTION_FATAL_ERROR_MESSAGE]: (state, payload) => {
        state.fatalError = payload;
    },
}

const actions = {
    [types.CLICK_CONNECTION_STATUS]: ({commit, state, roostState, dispatch}) => {
        let loading_trans = store.getters.loading_trans;
        commit(types.MUTATE_LOADING_HEADER, loading_trans.first_step_header);
        commit(types.MUTATE_LOADING_DESCRIPTION, loading_trans.first_step_description);
        commit(types.MUTATE_LOADING, true);
        if (state.connectionStatus) {
            let formData = new FormData();
            formData.append('ajax', 'true');
            formData.append('action', 'validate_step');
            formData.append('source_shop_url', state.sourceShopUrl);
            formData.append('step_number', '1');
            formData.append('to_step_number', '2');
            fetch(validate_url, {
                body: formData,
                method: "post"
            })
                .then(response => {
                    if (response.ok) {
                        return response.json();
                    } else {
                        commit(types.MUTATE_CONNECTION_FATAL_ERROR_MESSAGE,true);
                        commit(types.MUTATE_LOADING_DESCRIPTION, loading_trans.first_step_error);
                        setTimeout(()=>{
                            commit(types.MUTATE_LOADING, false);
                        }, 1000);
                        
                        throw new Error('Something went wrong');

                    }
                })
                .then((data) => {

                    dispatch(types.UPDATE_MIDDLE_STATES, data);

                    if (data.has_error) {
                        commit(types.MUTATE_LOADING_DESCRIPTION, loading_trans.first_step_error);
                        setTimeout(()=>{
                            commit(types.MUTATE_LOADING, false);
                        }, 1000);
                    }
                    else{
                        commit(types.MUTATE_LOADING_DESCRIPTION, loading_trans.first_step_done);

                    }
                });
        }
    },

    [types.UPDATE_MIDDLE_STATES]: ({commit, dispatch}, mainData) => {
        let data;
        if (mainData.mapping) {
            data = mainData
        }
        else {
            data = mainData.step_two
        }
        {
            let select_box_data = {
                multi_shops: {},
                languages: {},
                order_states: {},
                currencies: {},
                customer_groups: {}
            }
            let selected_select_box = {
                multi_shops: {},
                languages: {},
                order_states: {},
                currencies: {},
                customer_groups: {}
            }
            let selected_entity = {
                taxes: [],
                manufactures: [],
                categories: [],
                carriers: [],
                cart_rules: [],
                orders: [],
                cms: [],
                seo: [],
                products: [],
                employees: [],
                customers: [],
                catalog_price_rules: [],
                tax: [],
                message_threads: [],
                accessories: [],
            }
            let selected_additional_entity = {
                clear_data: [],
                migrate_recent_data: [],
                keep_id: [],
            }
            let process_entity = [];
            if (!mainData.has_error && !mainData.has_warning) {
                for (let i in data.mapping) {
                    for (let k in data.mapping[i]) {
                        Object.assign(select_box_data[i], {[data.mapping[i][k].source_name]: data.mapping[i][k].target})
                        for (let d in data.mapping[i][k].target) {
                            let selected = data.mapping[i][k].mapping;

                            if (selected.target_id == data.mapping[i][k].target[d].value.target_id && 
                                selected.source_id == data.mapping[i][k].target[d].value.source_id) {
                                Object.assign(selected_select_box[i], {[data.mapping[i][k].source_name]: JSON.stringify(selected)}) //, {id_mapping: data.mapping[i][k].id_mapping}]})
                            } else {
                                if (selected.target_id == 0 && 
                                selected.source_id == data.mapping[i][k].target[d].value.source_id) {
                                    Object.assign(selected_select_box[i], {[data.mapping[i][k].source_name]:  
                                        JSON.stringify({"target_id":data.mapping[i][k].target[0].target_id,"source_id":data.mapping[i][k].target[0].source_id})}) //, {id_mapping: data.mapping[i][k].id_mapping}]})
                                }
                            }
                        }

                    }
                }
                for (let entities in data.entity) {
                    if (data.entity[entities][0].mapping == 1) {
                        Object.assign(selected_entity[entities], [entities])
                    }
                    else {
                        Object.assign(selected_entity[entities], [])
                    }
                }
                // Check all entities is selected
                let all_selected_entity = false;
                for (let entities in data.entity) {
                    if (data.entity[entities][0].mapping == 1) {
                        all_selected_entity = true;
                    }
                    else {
                        all_selected_entity = false;
                        break;
                    }
                }
                // Set all selected state
                if (all_selected_entity) {
                    commit(types.MUTATE_CHANGE_ALL_SELECTED, true);
                } else {
                    commit(types.MUTATE_CHANGE_ALL_SELECTED, false);
                }

                for (let additional_entities in data.additional) {
                    if (data.additional[additional_entities][0].mapping == 1) {
                        Object.assign(selected_additional_entity[additional_entities], [additional_entities])
                    }
                    else {
                        Object.assign(selected_additional_entity[additional_entities], [])
                    }
                }

                for (let process in data.process) {
                   let p_obj = {};
                   p_obj['type'] = data.process[process].type;
                   p_obj['total'] = data.process[process].total;
                   process_entity.push(p_obj);

                }
                if (step_status == 3 || pause) {
                    commit(types.MUTATE_MIGRATION_STATUS, true)
                }

                commit('mutateSelectedComponent', 'app-configuration')
                commit(types.MUTATE_FETCH_SELECTED_OBJECT, selected_select_box)
                commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, selected_entity)
                commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, selected_additional_entity)
                commit(types.MUTATE_FETCH_PROCESS_ENTITY, process_entity)
                dispatch(types.FETCH_UPDATE_OBJECTS, select_box_data)


            }
            else {
                if (mainData.has_error) {
                    let errorInput = document.querySelector('#inputConnection')
                    commit(types.MUTATE_REG_STATUS, false)
                    commit(types.MUTATE_CONNECTION_ERROR_MESSAGE, mainData.errors)
                    errorInput.style.borderColor = "red";
                }
            }
        }
    },

    [types.CLICK_GENERATE_ZIP]: ({commit}, payload) => {
        let formData = new FormData();
        formData.append('ajax', 'true');
        formData.append('action', 'generate_zip');
        fetch(validate_url, {
            body: formData,
            method: "post"
        })
            .then(data => {
                window.location.href = ps_module_url + "/assets/migrationpro_bridge_connector_module.zip";

            })
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
