<template>
    <div id="mg_report">
        <div class="mg_report">
            <div class="mg_status d-flex justify-content-center">
                <div>
                    <div class="mg_status_img"><img src="../../assets/succec_icon.svg"></div>
                    <h3 class="success__title" style="margin-top: 20px;">{{tr.fourth_header1}}</h3>
                    <h3 class="success__title">{{tr.fourth_header2}}</h3>
                    <p>{{tr.fourth_header_description}}</p>

                   <div class="image-settings">
                        <div class="mg_status-left">
                            <h4>{{tr.information}}</h4>
                            <p>{{tr.link_1_description}}</p>
                            <button @click="openUrl(tr.link_href)">{{tr.link_2_description}}</button>
                        </div>
                    <div class="mg_status-right">
                        <img src="../../assets/settings-image.svg" alt="Settings">
                    </div>
                    </div>
                </div>
            </div>
            <div>
                <div class="d-flex justify-content-between mg_report__download"><p>{{tr.migration_report}}</p>
                    <div><a v-bind:href="tr.report_download_link" target="_blank"><img
                            src="../../assets/download.svg" alt=""><span>{{tr.report_download}}</span></a></div>
                </div>
                <div class="mg_report__table">
                    <b-table hover responsive :items="migrationItems" id="mg_info"></b-table>
                </div>
            </div>
        </div>
        
        <div class="mg_report_first_step">
            <button @click="openUrl(tr.enjoy_new_shop_url)" class="next_step__button">{{tr.enjoy_new_shop}}</button>
            <button @click="reutrnBackFromReport" class="next_step__button white_bg" >{{tr.new_migration}}</button>
        </div>

        <div class="mg_fatal_error">
            {{fatalError ? "Fatal Error" : ""}}
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapState} from 'vuex';
    import {store} from '../../store/store';
    import * as types from '../../store/type';

    export default {
        destroyed() {
            this.$store.commit(types.MUTATE_MIGRATION_REPORT_FATAL_ERROR, false)
        },
        computed: {
            ...mapGetters({
                migrationItems: types.MG_REPORT, //get report and use it inside table
                fatalError: types.MG_REPORT_FATAL, //get report and use it inside table
                tr: "step_fourth_trans",
                   
            }),


        },
        methods: {
//            click New Migration button and return first step
            ...mapActions({
                reutrnBackFromReport: types.CLICK_RETURN_BACK,
            }),
//            method use when click Report migration and will open migration report inside new blank
            openWarningLog() {
            },            
            openUrl(url)
            {
                window.open(url,'_blank')
            }
        },
    }
</script>

<style lang="scss">
    @import "../../scss/main";

    .image-settings {
        font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif !important;
        margin-top: 60px;
        float: left;
        width: 100%;
        border-radius: 3px;
        box-shadow: 0 1px 3px 0 rgba(63, 63, 68, 0.15), 0 0 0 1px rgba(63, 63, 68, 0.05);
        background-color: #ffffff;
        padding: 20px;

        .mg_status-left {
            float: left;

            h4 {
                margin-top: 0 !important;
                font-size: 16px !important;
                font-weight: 600;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.5;
                letter-spacing: normal;
                color: #212b36;
                margin-bottom: 20px;
            }
            p {
                font-size: 14px !important;
                font-weight: normal;
                font-style: normal;
                text-align: left !important;
                font-stretch: normal;
                line-height: 1.43;
                letter-spacing: normal;
                color: #212b36;
                margin-bottom: 20px;
            }
            button {
                border: 1px solid rgba(33,43,54,0.4);
                border-radius: 4px;
                background-color: transparent;
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.43;
                letter-spacing: normal;
                text-align: center;
                color: #212b36;
                padding: 8px 18px;
            }
        }

        .mg_status-right {
            float: right;
            margin-top: 22px;
        }
    }

    #mg_report {
        box-shadow: $box-shadow;
        background: white;
        .mg_fatal_error {
            color: red;
            font-style: italic;
            font-size: 11px;
            font-weight: 300;
            line-height: 1.73;
        }
        .mg_report_first_step {
            width: 100%;
            float: left;
            padding: 20px 50px;
            border-top: 1px solid $border-color;
            background: white;

            .next_step__button {
                float: right !important;
            }
            .white_bg{
                background-color: white!important;
                border: 1px #5c6ac4 solid!important;
                color: #5c6ac4!important;
                margin-right: 20px;
            }
        }
        .mg_report {
            margin-top: 30px;
            padding: 30px 50px;
            .mg_content__info {
                border-top: 1px solid $border-color;
                border-bottom: 1px solid $border-color;
                padding-top: 30px;
                margin-bottom: 50px;
                .mg_report__info {

                    img {
                        width: 16px;
                    }
                    h3 {
                        font-size: 18px;
                    }

                    span {
                        vertical-align: middle;
                    }
                }
            }
            .mg_report__information {
                margin-top: 20px;
                ol {
                    counter-reset: item;
                    padding: 0;
                }
                ol li {
                    display: block;
                    margin-bottom: 15px;
                    a {
                        width: 100%;
                    }
                    span {
                        color: #7f8fa4;
                        font-size: 14px;
                    }
                }
                ol li:before {
                    content: counter(item) "   ";
                    counter-increment: item;
                    color: #5c6ac4;
                    margin-right: 11px;
                    margin-left: 4px;
                }

                ol li:last-child {
                    margin-bottom: 28px !important;
                }
            }
            .mg_status {
                padding-bottom: 50px;
                h3 {
                    font-weight: bold;
                    font-size: 26px;
                    text-align: center;
                }
                p {
                    text-align: center;
                    font-size: 13px;
                    margin-top: 5px;
                    color: #656b6f;
                }
                .mg_status_img {
                    margin: 50px 0 25px 0;
                    text-align: center;
                }
            }
            .mg_report__download {
                p {
                    font-size: 16px;
                    margin-bottom: 14px !important;
                }
                span {
                    font-size: 14px;
                    color: #627381;
                }
                img {
                    margin-right: 5px;
                }
            }
            .mg_report__table {
                box-shadow: $box-shadow;
                border-radius: 5px;
                overflow: hidden;
            }
            #mg_info {
                margin-bottom: 0;
                thead {
                    background: #5C6AC4;
                    font-size: 14px;
                    color: white;
                    font-weight: 500;
                    th:first-child{
                        padding-left: 14px;
                    }
                }
                tbody {
                    tr {
                        td {
                            font-size: 14px;
                            color: #4d4f5c;
                            padding: 12px;
                        }
                    }
                }
            }
        }
    }

    .success__title {
        text-transform: initial !important;
        border: none !important;
        margin-bottom: 0 !important;
        height: 1.9em !important;
    }
</style>
