// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {store} from './store/store'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'


Vue.config.productionTip = false
Vue.use(BootstrapVue);

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: {App},
    template: '<App/>',
});
// set debug mode for module on server
(function() {
    window.setdebug= setdebug;
    function setdebug(value) {
        $.ajax({
            type: "POST",
            url: validate_url,
            async: false,
            dataType: 'json',
            data: {
                action: 'debug_on',
                ajax: 'true',
                turn: value
            },
            success: function () {
                if (value==1) {
                    console.log('you set debug mode to On')
                }else{
                    console.log('you set debug mode to Off')
                }

            },
            error: function () {
                 console.log('There is a problem, we cant access debug mode, plese check again')
            }
        });
    }
    })();
// set migration speed for module on server
    (function() {
        window.setspeed= setspeed;
        function setspeed(value) {
            $.ajax({
                type: "POST",
                url: validate_url,
                async: false,
                dataType: 'json',
                data: {
                    action: 'speed_up',
                    ajax: 'true',
                    speed: value
                },
                success: function () {
                    console.log('you have set migration speed to ' + value)
                },
                error: function () {
                    console.log('There is a problem, we cant set speed, please check again')
                }
            });
        }
    })();
