/**

*/

<template>
    <transition name="modal-fade">
        <div id="mg_modal_attention" v-if="modalVisibilityAttention">
            <div class="mg_modal_content">
                <div class="mg_modal_p">
                    <div class="mg_modal__close" @click="closeAttentionModal(false)">
                        <img src="../../assets/icon-close.png"></div>
                    <div class="mg_modal__title">
                        <div class="mg_modal__info">
                            <img src="../../assets/warning-icon.svg" alt=""><h4>{{tr.header_stop_modal}}</h4>
                        </div>
                        
                    </div>

                    <p class="stop-text">
                           {{tr.description_stop_modal}}</p>
                </div>

                <div class="mg_modal__button">
                    <button class="mg_modal__more" @click="closeAttentionModal(false)">{{tr.continue_button}}</button>
                    <button class="mg_modal__start" @click="reutrnBackConnection">{{tr.stop_button}}</button>
                </div>
            </div>
        </div>
    </transition>

</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapMutations} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from '../../store/type'

    export default {
        name: "modal-attention",

//        get data from store
        computed: {
            ...mapGetters({
//                get value modal visibility (show,hide)
                modalVisibilityAttention: types.ATTENTION_MODAL_VISIBILITY,
                tr: 'step_third_trans',
            }),
        },
//   change state with mutation or action
        methods: {
            ...mapActions({
//                return back first step when click stop migration
                reutrnBackConnection: types.CLICK_MIGRATION_RETURN_BACK,
// //                last step tranlation
//                 tr: 'attention_labels',
            }),
            ...mapMutations({
//                open modal (show)
                openAttentionModal: types.MUTATE_OPEN_ATTETION_MODAL,
//                close modal (hide)
                closeAttentionModal: types.MUTATE_CLOSE_ATTETION_MODAL,
            }),
        }
    }
</script>

<style lang="scss">
    @import "../../scss/main";

    #mg_modal_attention {
        .mg_modal_p {
            padding: 0 20px;
            position: relative;
        }
        .mg_modal__title {
            margin-bottom: 25px;
            p {
                font-size: 14px;
                line-height: 1.5;
            }
            .mg_modal__info {
                margin-bottom: 15px;
                h4 {
                    display: inline-block;
                    font-size: 18px;
                    vertical-align: bottom;
                    margin: 0 0 0 10px;
                }
            }
        }
        .mg_modal__close {
            text-align: right;
            img {
                width: 14px !important;
            }
        }
        .mg_modal__button {
            text-align: center;
            padding-bottom: 20px;
            padding-top: 20px;
            border-top: solid 1px #eaeef3;
        }
        .mg_modal__start{
            background-color: #ff0000!important;

        }


    }

    .mg_modal__title {
            margin-bottom: 20px;
            margin-left: -20px;
            margin-right: -20px;
            background-color: #fdf7e4;
            padding: 20px;
            border-top: 4px solid #eec200;
            border-top-left-radius: 3px;
            border-top-right-radius: 3px;
            box-shadow: 0 1px 3px 0 rgba(63, 63, 68, 0.15), 0 0 0 1px rgba(63, 63, 68, 0.05);
            
            img {
                float: left;
            }
            .mg_modal__info {
                margin-bottom: 8px;

                h4 {
                    display: inline-block;
                    font-size: 18px;
                    vertical-align: bottom;
                    margin: 0 0 0 10px;
                }
            }
        }

        

        .mg_modal__close {
            position: absolute;
            right: 12px;
            top: 20px;
        }

        .stop-text {
            font-size: 14px;
            margin-bottom: 20px !important;
        }
</style>
