import * as types from '../type'

const state = {
    show_loading: false,
    header: '',
    description: '',
}

const getters = {
    [types.LOADING]: (state) => {
        return state.show_loading;
    },
    [types.LOADING_HEADER]: (state) => {
        return state.header;
    },
    [types.LOADING_DESCRIPTION]: (state) => {
        return state.description;
    },
}

const mutations = {
    [types.MUTATE_LOADING]: (state, payload) => {
        state.show_loading = payload;
    },
    [types.MUTATE_LOADING_HEADER]: (state, payload) => {
        state.header = payload;
    },
    [types.MUTATE_LOADING_DESCRIPTION]: (state, payload) => {
        state.description = payload;
    },
}


export default {
    state,
    getters,
    mutations
}
