<template>
    <div class="loading" v-bind:class="loading ? 'show': 'hide'">
        <div class="loading_progress">
            <svg class="load" x="0px" y="0px" viewBox="0 0 150 150">
                <circle class="loading-inner" cx="75" cy="75" r="60"/>
            </svg>
        </div>
        <div class="loading_content">
            <h4>
                {{loading_header}}
            </h4>
            <span>
                {{loading_description}}
            </span>

        </div>

    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from '../../store/type'

    export default {
        name: 'loading',
        computed: {
            ...mapGetters({
                loading: types.LOADING,
                loading_header: types.LOADING_HEADER,
                loading_description: types.LOADING_DESCRIPTION,

            }),
        },
    }
</script>

<style lang="scss" scoped>
  @import "../../scss/main";

    .loading
    {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.92;
        -webkit-backdrop-filter: blur(50px);
        backdrop-filter: blur(50px);
        background-color: #f5f6f7;
        z-index: 556;

        .loading_progress {
            position: relative;
            width: 100%;
            margin-top: 15%;
            text-align: center;

            .load {
                width: 55px;
                animation: loading 3s linear infinite;
                    .loading-inner {
                        stroke: {
                        dashoffset: 0;
                        dasharray: 300;
                        width: 10;
                        miterlimit: 10;
                        linecap: round;
                    }
                    animation: loading-circle 2s linear infinite;
                    stroke: #51BBA7;
                    fill: transparent;
                    }
                }

            @keyframes loading {
                    0% { 
                transform: rotate(0); 
                }
                    100% { 
                transform: rotate(360deg);
                }
            }
            @keyframes loading-circle {
                    0% { 
                stroke-dashoffset: 0
                }
                    100% { 
                stroke-dashoffset: -600;
                }
            }
        }
        .loading_content{
            text-align: center;
            h4{
                font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
                font-size: 30px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #212b34;
                padding-bottom: 30px;
                padding-top: 50px;

            }
            span{
                font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
                font-size: 24px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #949494;
            }
        }
    }
</style>
 