<template>
    <transition name="slide">
        <div class="more_options__animation" v-if="toggleMoreOptions">
            <div class="more_options">
                <div class="more_options__content">
                    <h4>{{tr.entity}}</h4>
                    <div>
                        <b-form-group>
                            <div class="border_checkbox">
                                <b-form-checkbox
                                        @change="selectAll"
                                        id="toggleCheckbox"
                                        v-model="selectAllEntity"
                                >
                                    <span v-if="selectAllEntity" class="select-text">{{tr.unselect_all}}</span>
                                    <span v-if="!selectAllEntity" class="select-text">{{tr.select_all}}</span>
                                </b-form-checkbox>
                            </div>
                            <b-form-group>
                                <b-row class="select-wrapper">
                                    <b-col v-for="i in Math.ceil(mgData.length / 4)">
                                        <b-form-checkbox-group id="more_options__checkbox"
                                                               v-for="(item,index) in mgData.slice((i - 1) * 4, i * 4)"
                                                               name="flavour2" stacked
                                                               :name="checkboxSelected[item.value]"
                                                               v-model="checkboxSelected[item.value]"
                                                               :key="index"
                                                               @change="changeAllSelected($event),entityCheckbox($event, item.value)"
                                                               class="toggleCheckbox"
                                        >
                                            <b-form-checkbox
                                                    :value="item.value">{{item.text}}
                                            </b-form-checkbox>
                                        </b-form-checkbox-group>
                                    </b-col>
                                </b-row>
                            </b-form-group>
                        </b-form-group>
                    </div>
                </div>
                <div class="more_options__content additional-title">
                    <h4>{{tr.additional}}</h4>
                    <b-form-group>
                        <b-form-checkbox-group id="more_options__radio"
                                                class="more-options__additional"
                                               v-for="(item,index) in mgDataAdditional"
                                               name="flavour2" stacked
                                               v-model="checkboxSelectedAdditional[item.value]"
                                               :key="index"
                                               @change="additionalCheckbox($event)"
                                               :disabled="tr.demo_mode"
                        >
                            <b-form-checkbox  v-if="item.value=='clear_data'"
                                    :value="item.value">
                                {{tr.clean_data}}
                                <p>
                                    {{tr.clean_data_text}}
                                </p>
                            </b-form-checkbox>
                            <b-form-checkbox  v-if="item.value=='migrate_recent_data'"
                                    :value="item.value">
                                {{tr.recent_data}}
                                <p>
                                    {{tr.recent_data_text}}
                                </p>
                            </b-form-checkbox>
                            <b-form-checkbox  v-if="item.value=='keep_id'"
                                    :value="item.value">
                                {{tr.keep_id}}
                                <p>
                                    {{tr.keep_id_text}}
                                </p>
                            </b-form-checkbox>
                        </b-form-checkbox-group>
                    </b-form-group>

                </div>
                <div class="more_options__content">
                    <h4 v-text="Object.keys(multiShop).length > 1?tr.multi_stores:tr.single_store"></h4>
                    <div v-for="(i,index) in multiShop">
                        <label :key="index">{{index}}</label>
                        <select class="more_option__select" :name="index"
                                  v-model="selectedOptions['multi_shops'][index]">
                                  <option  v-for="(val,ind) in i" :value="JSON.stringify(val['value'])"
                                  >{{val['label']}}</option>
                                  </select>
                    </div>
                </div>

                <div class="more_options__content">
                    <h4>{{tr.currencies}}</h4>
                    <div v-for="(i,index) in currencies">
                        <label :key="index">{{index}}</label>                        
                        <select class="more_option__select" :name="index"
                                  v-model="selectedOptions['currencies'][index]">
                                  <option  v-for="(val,ind) in i" :value="JSON.stringify(val['value'])"
                                  >{{val['label']}}</option>
                                  </select>
                    </div>
                </div>
                <div class="more_options__content">
                    <h4>{{tr.languages}}</h4>
                    <div v-for="(i,index) in languages">
                        <label :key="index">{{index}}</label>
                        <select class="more_option__select" :name="index"
                                  v-model="selectedOptions['languages'][index]">
                                  <option  v-for="(val,ind) in i" :value="JSON.stringify(val['value'])"
                                  >{{val['label']}}</option>
                                  </select>

                    </div>
                </div>
                <div class="more_options__content">
                    <h4>{{tr.customer_groups}}</h4>
                    <div v-for="(i,index) in customerGroup">
                        <label :key="index">{{index}}</label>
                        <select class="more_option__select" :name="index"
                                  v-model="selectedOptions['customer_groups'][index]">
                                  <option  v-for="(val,ind) in i" :value="JSON.stringify(val['value'])"
                                  >{{val['label']}}</option>
                                  </select>
                    </div>
                </div>
                <div class="more_options__content">
                    <h4>{{tr.order_status}}</h4>
                    <div v-for="(i,index) in orderStates">
                        <label :key="index">{{index}}</label>
                        <select class="more_option__select" :name="index"
                                  v-model="selectedOptions['order_states'][index]">
                                  <option  v-for="(val,ind) in i" :value="JSON.stringify(val['value'])"
                                  >{{val['label']}}</option>
                                  </select>
                    </div>
                </div>
                <div class="more_options__content">
                    <h4>{{tr.speed}}</h4>
                    <div class="d-flex flex-wrap ranger__mg_top">
                        <div class="more_options_r_label"><p>{{tr.sped_advice}}</p></div>
                        <app-ranger/>
                    </div>
                </div>
                <div class="more_option_error" v-if="fatalErrorSecond"> <img src='../../assets/alert-error.svg'/> Fatal Error</div>
                <div class="more_option__start_mg d-flex flex-column align-items-end">
                    <div v-if="configurationErrorStatus" class="more_option_error">
                        <span v-for="error in configurationErrorMessage">*{{error}}</span>
                    </div>
                    
                    <div class="more_option__button_holder">
                        <a @click.prevent="backConnection" href="#">{{tr.back}}</a>
                        <button @click="openModal(true)"
                        v-scroll-to="{element: '.ranger__mg_top', duration: 1000, offset: -530 }" class="next_step__button">{{tr.start_migration}}</button>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>

    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    import {mapState} from 'vuex';
    import * as types from '../../store/type'
    import Vue from 'vue'
    import Ranger from '@/components/components/Ranger'
    import vSelect from 'vue-select'

    Vue.component('app-ranger', Ranger)
    export default { //migrationStatus('moreOptions')
        name: "MoreOptions",
        destroy() {
            this.$store.commit(types.MUTATE_FATAL_ERROR_SECOND, false);
            this.$el.removeEventListener('scroll', this.handleScroll);
        },
        updated() {
            //     if all entity select allow to activate migration component
            let checkbox_selected = this.$store.getters['moreOptions/CHECKBOX_SELECTED']
            let allow_enter_migrate = false
            let disable_migration_button = document.querySelector('.more_option__button_holder .next_step__button')

            for (let entity in checkbox_selected) {
                if (checkbox_selected[entity].length == 1) {
                    allow_enter_migrate = true
                }
            }
            if (!allow_enter_migrate) {
                disable_migration_button.classList.add('disable_button')

            } else {
                disable_migration_button.classList.remove('disable_button')

            }
        },
        data() {
                return {
                        scrolled: false,
                    };
                },
        mounted() {
                this.$el.addEventListener('scroll', this.handleScroll);
                },
        computed: {
            ...mapGetters({
                orderStates: types.ORDER_STATE, //get order states states
                multiShop: types.MULTI_STORE, //get multi shop state
                languages: types.LANGUAGE,//get language state
                currencies: types.CURRENCIES,//get currencies shop state 
                customerGroup: types.CUSTOMER_GROUP,//get customer group state
                mgData: types.MG_DATA,//migrated data state
                checkboxSelected: types.CHECKBOX_SELECTED,//checkbox selected state
                toggleMoreOptions: types.TOGGLE_MORE_OPTIONS,//get toggle more options state
                selectedOptions: types.SELECTED_OPTION,//get current option selected in selectbox
                mgDataAdditional: types.MG_ADDITIONAL_DATA,//get additional data for migration
                checkboxSelectedAdditional: types.CHECKBOX_ADDITIONAL_SELECTED,//addition ceckbpx selected state
                selectAllEntity: types.SELECT_ALL_ENTITY,//select all entity state
                configurationErrorStatus: types.CONFIGURATION_ERROR_STATUS,//get error state
                configurationErrorMessage: types.CONFIGURATION_ERROR,// get error message
                fatalErrorSecond: types.FATAL_ERROR_SECOND,
                tr: 'step_second_trans',
            }),

        },
        methods: {
            ...mapActions({
                backConnection: types.CLICK_BACK_CONNECTION, // click reutrn first step
                migrationStatus: types.CLICK_MIGATE, // click start migration process
                openModal: types.CLICK_OPEN_MODAL, // click  and open modal

//                checkBoxState: types.CLICK_CHECKBOX_STATE,
//                radioState: types.CLICK_SELECTED_RADIO,
                changeAllSelected: types.CHANGE_ALL_SELECTED_ENTITY,// click select all entity
                selectAll: types.CLICK_SELECT_ALL_ENTITY // click select all entity
            }),
            handleScroll(e) {
                    this.scrolled = e.target.scrollTop > 0;
                },
//            if clear data checked then uncheck migrate recent or on the contrary
            additionalCheckbox(event) {
                if (event == 'clear_data') {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {migrate_recent_data: []})
                }
                if (event == 'migrate_recent_data') {
                    
                    let customerChecked = document.querySelectorAll('[true-value="customers"]')
                    let ordersChecked = document.querySelectorAll('[true-value="orders"]')
                    let productChecked = document.querySelectorAll('[true-value="products"]')

                    if(customerChecked[0].name.length == 0 && ordersChecked[0].name.length == 0 && productChecked[0].name.length == 0)
                    { 
                        this.$store.commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {migrate_recent_data: []})
                        return;
                    }

                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ADDITIONAL_OBJECT, {clear_data: []})
                }
            },

//            if customer checked then allow check order and cart rules
            entityCheckbox(event, entity) {
                // console.log(entity);
                //
                let customerChecked = document.querySelectorAll('[true-value="customers"]')
                let ordersChecked = document.querySelectorAll('[true-value="orders"]')
                let cart_rulesChecked = document.querySelectorAll('[true-value="cart_rules"]')
                let message_threadsChecked = document.querySelectorAll('[true-value="message_threads"]')
                let accessoriesChecked = document.querySelectorAll('[true-value="accessories"]')

                if(event[0] == null && entity == 'customers')
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {customers: []});
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {orders: []});
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {cart_rules: []});
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {message_threads: []});
                } 
                else if(event[0] == null && entity == 'orders')
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {orders: []});
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {message_threads: []});
                }
                else if(event[0] == null && entity == 'products')
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {products: []});
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {accessories: []});
                }


                if(event[0] == 'orders' && ordersChecked[0].name.length == 0)
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {orders: ['orders']});
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {customers: ['customers']})
                }
                else if(event[0] == 'cart_rules' && cart_rulesChecked[0].name.length == 0)
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {cart_rules: ['cart_rules']})
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {customers: ['customers']})
                }
                else if(event[0] == 'message_threads' && message_threadsChecked[0].name.length == 0)
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {message_threads: ['message_threads']})
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {orders: ['orders']})
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {customers: ['customers']})
                }
                else if(event[0] == 'accessories' && accessoriesChecked[0].name.length == 0)
                {
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {accessories: ['accessories']})
                    this.$store.commit(types.MUTATE_FETCH_SELECTED_ENTITY_OBJECT, {products: ['products']})
                }

            }
        },

        watch: {
//            watch select all state

            checkboxSelected(newVal, oldVal) {
                for (let i in newVal) {
                    if (newVal[i].length === 0) {
                        this.$store.commit(types.MUTATE_CHANGE_ALL_SELECTED, false)
                    } else {
                        this.$store.commit(types.MUTATE_CHANGE_ALL_SELECTED, true)
                    }
                }
            },
        }
    }

</script>

<style lang="scss">
    @import "../../scss/main";

    .more_options__animation {
        overflow: hidden;
        transition: all 1s ease;
        box-shadow: $box-shadow;
    }

    #more_options__checkbox {
        .custom-control {
            color: #212b36;
            /*font-size: 14px;*/
            margin-bottom: 20px;
            display: flex;
            align-items: center;
            padding-left: 1.8rem !important;
        }
    }

    #more_options__checkbox_preserve {
        .custom-control {
            padding-bottom: 10px;
            margin: 15px 0 15px 0;
        }
    }

    #more_options__radio {
        .custom-control-label {
            p {
                font-size: 12px;
                font-weight: 400;
                color: #212b36;
                line-height: 1.73;
                margin-top: 8px;
            }
        }
    }

    .more_option__start_mg {
        padding-top: 30px;
        border-top: solid 1px #eaeef3;
        .more_option__button_holder {
            a {
                color: #979797;
                margin-right: 20px;
                font-size: 14px;
            }
        }
    }

    .more_options {
        padding: 28px 45px 20px 45px !important;
        .more_option_error {
            // position: absolute;
            color: #bf0711;
            font-size: 14px;
            font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
            margin-left: 0px;
            margin-bottom: 10px;

            span {display: block}
        }
        .more_option__select {
            margin-bottom: 20px;
            background: white;
            .dropdown-toggle {
                height: 36px;
            }
            .dropdown-toggle {
                &:after {
                    display: none !important;
                }
                .vs__actions {
                    margin-right: 5px;
                    button {
                        display: none;
                    }
                    .open-indicator {
                        &:before {
                            border-width: 2px 2px 0 0;
                        }
                    }
                }
            }
            .dropdown-menu {
                overflow: initial;
                background: white !important;
                &:focus {
                    background: white !important;
                }
                li {
                    background: white !important;
                    &:hover {
                        color: white;
                    }
                    &:focus {
                        background: white !important;
                    }
                }
            }
        }
        box-shadow: $box-shadow;
        .more_options_r_label {
            flex: 1 0 auto;
            padding-top: 15px;

            p {
                margin: 0;
                font-size: 14px;
                font-weight: 500;
            }
        }
        .border_checkbox {
            display: inline-block;
            border-bottom: solid 1px #ececec;
            padding-bottom: 9px;
            margin: 11px 0 20px 0;
        }
        padding: 35px 45px 15px 45px;
        background: #f5f6f7;
        font-family: BlinkMacSystemFont, -apple-system, Arial, 'Segoe UI', Roboto, Helvetica, sans-serif;
        .more_options__content {
            margin-bottom: 24px;
            .more_option__select{
                position: relative;
                font-size: 16px !important;
                .dropdown-menu{
                    padding: 0;
                    position: absolute;
                    top:1px;
                    z-index: 1;
                    li{
                        a{
                            padding:8px;
                            font-size: 15px;
                        }
                    }
                }
                .selected-tag{
                    width: 100%;
                }
            }
            label{
                font-size: 14px;
                color: #212b36;
            }
            h4 {
                margin-top: 50px !important;
                font-size: 20px;
                color: #212b36;
                border-bottom: solid 1px #ececec;
                padding-bottom: 15px;
            }
        }
        .ranger__mg_top {
            margin-top: 25px;
        }
    }

    .select-text {
        vertical-align: middle;
    }

    .select-wrapper .col {
        padding: 0 !important
    }

    .custom-control-label:after, 
    .custom-control-label:before {
        left: -1.6rem !important;
    }

    .additional-title h4 {
        margin-bottom: 20px !important;
    }

    .more-options__additional .custom-control-label {
        margin-bottom: 16px !important;
    }

    .v-select .open-indicator:before {
        border-color: #838eab !important;
        height: 8px !important;
        width: 8px !important;
    }
</style>
